.my-input {
  width: 30%;
  padding: 12px 20px;
  border-radius: 18px;
  margin: 8px 0;
  /* box-sizing: border-box; */
  border: 2px solid transparent;
  background-color: wheat;
  color: rgb(118, 18, 18);
  font-size: 25px;
  font-family: 'Open Sans', serif;
}

.container {
  font-family: 'Times New Roman', Times, serif;
}

.my-input:focus {
  outline: none; /* Removes the focus outline */
  border: none; /* Ensures no border appears on focus */
}

.button {
  left: 10%;
  padding: 10px 20px;
  border: none;
  border-radius: 18px; /* Rounded corners */
  /* background-image: linear-gradient(to right, #6a11cb 0%, #2575fc 100%); */
  color: white;
  font-size: 16px;
  font-family: 'Roboto Mono', sans-serif;
  cursor: pointer;
  outline: none;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.button:hover {
  /* background-image: linear-gradient(to right, #2575fc 0%, #6a11cb 100%); */
  opacity: 0.7;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}

.button:active {
  transform: translateY(2px);
}


.my-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60%;
  margin: auto;
  padding: 20px;
  border: 1px black;
  border-radius: 8px;
  font-size: 30px;
}

    #content-container {
      min-height: 3000px;
      display: block;
      overflow: hidden;
      width: 100%;
      font-family: monospace;
    }
    
    .footer {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-radius: 5px;
      bottom: 0;
      width: 100%; 
      height: 7%;
      border: solid 1px grey;
      background: radial-gradient(circle,  #B9B9BB, #CBCBCD, #DDDDDF, #FFFFFF);
      padding: 20px;
    } 
    
    .footer-text {
      font-size: 1em;
      margin: 0;
      color: brown;
    }
    
    .footer-line {
      width: 100%;
      height: 1px;
      background-color: brown;
      margin-top: 20px
    }
    
    .resources {
      padding: 0.8em;
      font-size: 0.8em;
    }
    
    
    .my-simple-input {
      position: absolute;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      top: 15vh;
      left: 15%;
      justify-content: space-between;
      width: 70%;
      padding: 12px 20px;
      border-radius: 5px;
      margin: 8px 0;
      border: 1px solid grey;
      background: linear-gradient(to left, #CBCBCD, #DDDDDF, #FFFFFF); 
      color: rgb(118, 18, 18);
      font-size: 20px;
      font-family: monospace;
    }

    
    .my-simple-input:focus,
    .my-simple-input:active,
    .my-simple-input:hover,
    .my-simple-input:visited
    {
      border: 1px solid grey !important; /* Keeps the border white on focus */
      outline: none !important; /* Removes the focus outline */
    }
    
    .my-simple-input input {
      border: none;
      background: transparent;
      outline: none; 
      color: inherit;
      font-size: inherit;
      font-family: inherit; 
      width: 96%; 
    } 
    
    
    .results-bar {
      margin-top: 1%;
      position: absolute;
      display: flex;
      flex-direction: column;
      border: 1px solid grey;
      border-radius: 5px;
      top: 25vh;
      left: 15%;
      width: 70%;
      align-items: center;
      justify-content: center;
      position: absolute;
      background: linear-gradient(to right, #CBCBCD, #DDDDDF, #FFFFFF); 
    }

.e-number-card {
  width: 99%;
  display: block;
  border: 1px solid grey;
  border-radius: 3px;
  /* margin-top: 5px; */
  margin-left: 10px;
  /* margin-right: 10px; */
  margin-top: 3px;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 10px;
  padding-left: 10px;
  transition: transform 0.3s ease;
}

.e-number-card:hover {
  opacity: 0.7;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
  /* height: 20%; */
  transform: scale(1.2, 1.2); 
  /* background-color: #f1f1f1; */
  /* height: 20%; */
}

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; /* Align items to the start of the container */
  height: 100vh; /* Take up the full height of the viewport */
  padding-top: 5vh; /* Adjust this value to control how far down from the top the input appears */
}

.curtain {
  position: absolute;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: baseline;
  top: 1vh;
  border-radius: 15px;
  border: 1px solid grey;
  left: 30%;
  width: 70%;
  height: 90%;
  backdrop-filter: blur(5px);
  font-family: inherit;
  background: linear-gradient(to left, rgba(185, 185, 187, 0.5), rgba(203, 203, 205, 0.5), rgba(221, 221, 223, 0.5));
  color: brown;
  transition: transform 0.8s ease-out; /* Animate the movement */
}

.curtain-move-right {
  transform: translateX(100%);
}